.resume-section {
  padding: 4rem 0;
  background-color: #f8f9fa;
  position: relative;
  overflow: hidden;
}

.resume-bg-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 25% 10%, rgba(44, 110, 73, 0.05) 0%, transparent 8%),
    radial-gradient(circle at 75% 30%, rgba(44, 110, 73, 0.05) 0%, transparent 8%),
    radial-gradient(circle at 15% 60%, rgba(44, 110, 73, 0.05) 0%, transparent 10%),
    radial-gradient(circle at 85% 80%, rgba(44, 110, 73, 0.05) 0%, transparent 10%);
  z-index: 0;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.container.compact {
  max-width: 1100px;
}

/* Section Header - More Compact */
.section-header {
  text-align: center;
  margin-bottom: 2rem;
}

.section-title {
  font-size: 2.25rem;
  color: #2C6E49;
  font-weight: 700;
  letter-spacing: -0.5px;
  margin-bottom: 0.5rem;
}

.section-subtitle {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 1.5rem;
}

.section-heading {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  position: relative;
}

.section-heading.center {
  justify-content: center;
  flex-direction: column;
}

.section-heading h3 {
  font-size: 1.5rem;
  color: #2C6E49;
  margin-left: 0.75rem;
}

.section-heading .section-icon {
  font-size: 1.5rem;
  color: #2C6E49;
}

.section-heading.center .section-icon {
  margin-bottom: 1rem;
  font-size: 2.2rem;
}

.section-heading.center h3 {
  margin-left: 0;
  margin-bottom: 1rem;
}

/* Profile Card - Compact */
.resume-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.07);
  padding: 0;
  margin-bottom: 1.5rem;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.profile-card {
  position: relative;
}

.profile-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.1);
}

.resume-profile {
  display: flex;
  align-items: center;
  padding: 1.5rem;
  gap: 2rem;
  position: relative;
}

.resume-image-container {
  flex-shrink: 0;
  position: relative;
}

.resume-profile-image {
  width: 150px;
  height: 150px;
  border-radius: 12px;
  object-fit: cover;
  box-shadow: 0 8px 20px rgba(44, 110, 73, 0.2);
  transition: transform 0.3s ease;
  border: 3px solid white;
}

.profile-badge {
  position: absolute;
  bottom: -12px;
  right: -12px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #2C6E49;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  box-shadow: 0 4px 12px rgba(44, 110, 73, 0.3);
  border: 3px solid white;
}

.resume-intro {
  flex-grow: 1;
}

.resume-intro h3 {
  font-size: 1.8rem;
  margin-bottom: 0.4rem;
  color: #333;
}

.resume-title {
  font-size: 1.1rem;
  color: #2C6E49;
  font-weight: 600;
  margin-bottom: 0.75rem;
}

.resume-tagline {
  font-size: 1rem;
  color: #666;
  line-height: 1.5;
  margin-bottom: 1.25rem;
}

.resume-cta {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.primary-btn,
.secondary-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  padding: 0.6rem 1.25rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 0.95rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.primary-btn {
  background-color: #2C6E49;
  color: white;
  box-shadow: 0 4px 12px rgba(44, 110, 73, 0.3);
}

.primary-btn:hover {
  background-color: #214d35;
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(44, 110, 73, 0.4);
}

.secondary-btn {
  background-color: #f1f1f1;
  color: #333;
  border: 1px solid #e1e1e1;
}

.secondary-btn:hover {
  background-color: #e8e8e8;
  transform: translateY(-3px);
}

/* New Resume Grid Layout */
.resume-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

/* Enhanced card styling for equal height and green themed elements */
.edu-exp-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.grid-section {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Experience Cards - Equal Height & More Green Elements */
.cards-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  height: 100%;
}

.experience-card {
  background-color: white;
  border-radius: 10px;
  padding: 0;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  transition: all 0.3s ease;
  border-top: 4px solid #ddd;
  display: flex;
  flex-direction: column;
  position: relative;
}

.experience-card.equal-height {
  flex: 1;
  min-height: 300px; /* Ensure minimum height */
}

.experience-card.green-border {
  border-top-color: #2C6E49;
}

.experience-card.compact {
  border-radius: 8px;
}

.experience-card.featured {
  border-top-color: #2C6E49;
}

.experience-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
  border-top-color: #2C6E49;
}

.card-header {
  padding: 1.25rem 1.5rem 0.75rem;
  border-bottom: 1px solid #f0f0f0;
  background: linear-gradient(to right, rgba(44, 110, 73, 0.05), transparent);
}

.card-year {
  display: inline-block;
  background-color: rgba(44, 110, 73, 0.1);
  color: #2C6E49;
  padding: 0.3rem 0.8rem;
  font-size: 0.8rem;
  border-radius: 50px;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.card-header h4 {
  font-size: 1.2rem;
  color: #333;
  margin: 0;
}

.card-body {
  padding: 1rem 1.5rem 1.25rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.card-location {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 0.8rem;
}

.card-details {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-details p {
  color: #555;
  line-height: 1.5;
  margin-bottom: 1rem;
}

/* Add a green accent to the bottom of cards */
.card-accent {
  height: 5px;
  background: linear-gradient(to right, #2C6E49, #4CAF50);
  margin-top: auto;
}

.tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  list-style: none !important;
  padding-left: 0 !important;
  margin: 0.5rem 0;
}

.tag-list li {
  background-color: #f5f5f5;
  padding: 0.3rem 0.8rem;
  border-radius: 50px;
  font-size: 0.8rem;
  color: #444;
  display: inline-block;
}

.tag-list li::before {
  display: none !important;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-top: 0.75rem;
}

.tech-stack span {
  background-color: rgba(44, 110, 73, 0.08);
  padding: 0.3rem 0.8rem;
  border-radius: 50px;
  font-size: 0.8rem;
  color: #2C6E49;
  border: 1px solid rgba(44, 110, 73, 0.1);
}

.tag-list span:hover,
.tech-stack span:hover {
  background-color: #2C6E49;
  color: white;
  transform: translateY(-2px);
}

/* Skills Section - Compact */
.skills-section {
  margin-bottom: 1.5rem;
}

.skills-section.compact {
  margin-bottom: 0;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.skill-category-card {
  background-color: white;
  border-radius: 10px;
  padding: 1.5rem;
  text-align: center;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  border-bottom: 3px solid transparent;
}

.skill-category-card.compact {
  padding: 1.25rem 1rem;
}

.skill-category-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
  border-bottom-color: #2C6E49;
}

.skill-icon-small {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background-color: rgba(44, 110, 73, 0.1);
  color: #2C6E49;
  border-radius: 50%;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.skill-category-card h4 {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 1rem;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  justify-content: center;
}

.skill-tags span {
  background-color: #f5f5f5;
  padding: 0.3rem 0.8rem;
  border-radius: 50px;
  font-size: 0.8rem;
  color: #444;
  transition: all 0.3s ease;
}

.skill-tags span:hover {
  background-color: #2C6E49;
  color: white;
  transform: translateY(-2px);
}

/* Contact Form Section - Compact */
.contact-form-section {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.07);
}

.contact-form-section.compact {
  padding: 1.5rem;
}

.contact-intro {
  margin-bottom: 2rem;
  color: #666;
  font-size: 1.1rem;
  text-align: center;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group.full-width {
  grid-column: 1 / -1;
}

.input-group {
  position: relative;
  display: flex;
  align-items: center;
}

.input-icon {
  position: absolute;
  left: 12px;
  color: #2C6E49;
  font-size: 0.9rem;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 0.8rem 1rem 0.8rem 2.4rem;
  border: 1px solid rgba(44, 110, 73, 0.2);
  border-radius: 6px;
  font-size: 0.95rem;
  transition: all 0.3s;
  background-color: #f9f9f9;
}

.contact-form textarea {
  padding: 0.8rem;
  resize: vertical;
  min-height: 80px;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  border-color: #2C6E49;
  box-shadow: 0 0 0 3px rgba(44, 110, 73, 0.15);
  background-color: white;
}

.submit-btn {
  grid-column: 1 / -1;
  padding: 0.9rem 1.5rem;
  background-color: #2C6E49;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s;
  display: inline-block;
  text-align: center;
  box-shadow: 0 4px 12px rgba(44, 110, 73, 0.25);
}

.submit-btn:hover {
  background-color: #214d35;
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(44, 110, 73, 0.3);
}

.submit-btn.loading {
  background-color: #4a8169;
  cursor: not-allowed;
}

/* Success message - More compact */
.success-message {
  text-align: center;
  padding: 1.5rem 1rem;
}

.success-icon {
  font-size: 2.5rem;
  color: #2C6E49;
  margin-bottom: 1rem;
}

.success-message h4 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: #2C6E49;
}

.success-message p {
  color: #666;
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.send-another-btn {
  padding: 0.7rem 1.4rem;
  background-color: #2C6E49;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 0.9rem;
}

.send-another-btn:hover {
  background-color: #214d35;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(44, 110, 73, 0.25);
}

/* Add this to your existing CSS */

.error-message {
  grid-column: 1 / -1;
  color: #e53935;
  background-color: rgba(229, 57, 53, 0.1);
  padding: 0.7rem 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 0.9rem;
}

@media (max-width: 992px) {
  .resume-sections {
    grid-template-columns: 1fr;
  }
  
  .edu-exp-grid {
    grid-template-columns: 1fr;
  }
  
  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .section-title {
    font-size: 2.2rem;
  }

  .resume-profile {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
  }

  .profile-badge {
    right: 50%;
    transform: translateX(50%);
  }

  .resume-cta {
    justify-content: center;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .contact-form-section {
    padding: 2rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .contact-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 576px) {
  .container {
    width: 95%;
  }
  
  .section-title {
    font-size: 1.8rem;
  }
  
  .resume-profile-image {
    width: 120px;
    height: 120px;
  }
}
