@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Playfair+Display:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --color-bg-primary: #F7F3E9;
  --color-bg-secondary: #cccaca;
  --color-header-bg: #ffffffe8;
  --color-section-1: #F7F3E9;
  --color-section-2: #EDEDED;
  --color-section-3: #F5F1E6;
  --color-text-primary: #333333;
  --color-text-secondary: #666666;
  --color-primary: #A8E6CF;
  --color-secondary: #74B49B;
  --color-accent: #2C6E49;
  --color-highlight: #E6C07A;
  --color-about-accent: #b9ebda;
  --color-project-accent: #d9e6c3;
  --color-hero-gradient-1: #A8E6CF;
  --color-hero-gradient-2: #74B49B;
  --color-testimonial-bg: #F0F5F1;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
}

body {
  background-color: var(--color-bg-primary);
  color: var(--color-text-primary);
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  position: relative;
  overflow-x: hidden;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at top right, rgba(116, 180, 155, 0.08), transparent 70%),
              radial-gradient(circle at bottom left, rgba(168, 230, 207, 0.08), transparent 70%);
  pointer-events: none;
  z-index: -1;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
  line-height: 1.3;
  letter-spacing: 0.3px;
  font-weight: 500;
  color: var(--color-text-primary);
}

/* Section background colors */
header, .navbar {
  background-color: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.03);
  position: sticky;
  top: 0;
  z-index: 100;
}

#about {
  background: linear-gradient(135deg, var(--color-section-1) 0%, var(--color-about-accent) 100%);
  position: relative;
  overflow: hidden;
}

#about::before {
  content: "";
  position: absolute;
  top: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: rgba(168, 230, 207, 0.1);
  z-index: 0;
}

#skills, #contact {
  background-color: var(--color-section-2);
}

#projects {
  background: linear-gradient(135deg, var(--color-section-3) 0%, var(--color-project-accent) 100%);
  position: relative;
}

#projects::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30%;
  background: linear-gradient(180deg, transparent, rgba(116, 180, 155, 0.08));
  z-index: 0;
}

#experience {
  background-color: var(--color-primary);
}

.section-container {
  padding-top: 4rem;
  padding-bottom: 4rem;
}

.hero-section {
  min-height: calc(100vh - 80px);
  background: linear-gradient(120deg, #E6F7F1 0%, #F7F3E9 100%);
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
}

/* Replace the SVG pattern with a warmer, softer pattern */
.hero-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 30%, rgba(230, 192, 122, 0.15) 0%, transparent 25%),
    radial-gradient(circle at 80% 70%, rgba(168, 230, 207, 0.15) 0%, transparent 25%),
    url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%2374B49B' fill-opacity='0.08'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 1;
}

/* Add animated shapes to the background */
.hero-section::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 30%;
  background: linear-gradient(0deg, white, transparent);
  z-index: 1;
}

/* Add these new classes for additional decorative elements */
.hero-bg-circle-1 {
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(168, 230, 207, 0.3) 0%, transparent 70%);
  top: 10%;
  right: 5%;
  z-index: 0;
  animation: pulse 8s ease-in-out infinite alternate;
}

.hero-bg-circle-2 {
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(230, 192, 122, 0.2) 0%, transparent 70%);
  bottom: 15%;
  left: 10%;
  z-index: 0;
  animation: pulse 6s ease-in-out 1s infinite alternate;
}

/* Add this animation */
@keyframes pulse {
  0% { transform: scale(1); opacity: 0.5; }
  100% { transform: scale(1.2); opacity: 0.8; }
}

#testimonials {
  background-color: #F0F5F1;
  position: relative;
  overflow: hidden;
  padding-top: 6rem;
  padding-bottom: 6rem;
  min-height: 80vh;
  border-top: 4px solid var(--color-primary);
  border-bottom: 4px solid var(--color-primary);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05) inset;
}

#testimonials::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 20% 30%, rgba(168, 230, 207, 0.2) 0%, transparent 30%),
    radial-gradient(circle at 80% 70%, rgba(230, 192, 122, 0.2) 0%, transparent 30%);
  top: 0;
  left: 0;
  opacity: 1;
  z-index: 0;
}

@layer components {
  .section-container {
    @apply container mx-auto px-4 md:px-8 max-w-7xl relative;
  }
  
  .section-title {
    @apply text-3xl md:text-4xl font-medium text-center mb-3 text-[#333333] relative inline-block;
  }
  
  .section-title::after {
    @apply content-[''] absolute bottom-0 left-1/2 -translate-x-1/2 w-16 h-[2px] bg-gradient-to-r from-[#A8E6CF] to-[#74B49B];
  }
  
  .section-subtitle {
    @apply text-center mb-12 text-[#666666] max-w-2xl mx-auto text-lg;
  }
  
  .btn-primary {
    @apply bg-gradient-to-r from-[#A8E6CF] to-[#74B49B] text-[#333333] font-medium py-2 px-6 rounded-lg transition-all duration-300 shadow-sm hover:shadow-md flex items-center justify-center border border-[#A8E6CF]/20;
  }
  
  .btn-secondary {
    @apply bg-transparent border border-[#74B49B] text-[#333333] hover:border-[#2C6E49] hover:text-[#2C6E49] font-medium py-2 px-6 rounded-lg transition-all duration-300 flex items-center justify-center;
  }
  
  .input-field {
    @apply w-full bg-white border border-[#EDEDED] rounded-lg p-3 focus:outline-none focus:border-[#74B49B] text-[#333333] shadow-sm;
  }

  .input-field::placeholder {
    @apply text-[#666666]/70;
  }
  
  .card {
    @apply bg-white rounded-xl shadow-sm hover:shadow-md overflow-hidden transition-all duration-300 relative border border-[#EDEDED];
  }
  
  .card::before {
    @apply content-[''] absolute inset-0 border border-transparent opacity-0 transition-all duration-500 rounded-xl;
    background: linear-gradient(130deg, #A8E6CF, #74B49B, #E6C07A, #A8E6CF) border-box;
    -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
  }
  
  .card-hover {
    @apply hover:shadow-lg hover:-translate-y-1 transition-all duration-300;
  }

  .card-hover:hover::before {
    @apply opacity-50;
  }
  
  .soft-gradient {
    @apply bg-gradient-to-r from-[#A8E6CF] to-[#74B49B];
  }
  
  /* Icon styling */
  .icon {
    @apply text-[#74B49B] hover:text-[#2C6E49] transition-colors duration-300;
  }
  
  .icon-circle {
    @apply bg-white p-3 rounded-full text-[#74B49B] hover:text-white hover:bg-gradient-to-r hover:from-[#A8E6CF] hover:to-[#74B49B] transition-all duration-300 border border-[#EDEDED] shadow-sm;
  }
  
  /* Background text elements */
  .bg-text {
    @apply text-[#EDEDED] font-bold opacity-70 absolute -z-10 text-8xl md:text-9xl select-none;
  }

  /* UI elements - softened */
  .soft-border {
    @apply relative p-0.5 rounded-lg overflow-hidden;
    background: linear-gradient(130deg, #A8E6CF, #74B49B, #E6C07A, #A8E6CF);
    background-size: 300% 300%;
    animation: soft-border-animate 10s ease infinite;
  }
  
  .soft-panel {
    @apply relative bg-white p-6 border-l-2 border-[#74B49B];
  }
  
  .soft-panel::before {
    @apply content-[''] absolute top-0 left-0 w-1/4 h-[1px] bg-[#A8E6CF];
  }
  
  .soft-panel::after {
    @apply content-[''] absolute bottom-0 left-0 w-1/3 h-[1px] bg-[#A8E6CF];
  }

  /* Glass card effect */
  .glass-card {
    @apply bg-white/80 backdrop-blur-sm rounded-xl border border-white/50 shadow-sm;
  }

  /* Highlight elements */
  .highlight-text {
    @apply text-[#2C6E49] font-medium;
  }
  
  .highlight-badge {
    @apply bg-[#E6C07A]/20 text-[#333333] px-3 py-1 rounded-full text-sm border border-[#E6C07A]/30;
  }

  /* About section specific styles */
  .about-image-container {
    @apply relative rounded-full p-1 overflow-hidden;
    background: linear-gradient(45deg, #A8E6CF, #74B49B, #E6C07A);
  }
  
  .about-image {
    @apply rounded-full border-4 border-white;
  }
  
  .about-bio {
    @apply relative z-10 bg-white/90 backdrop-blur-sm rounded-xl p-6 shadow-md border-l-4 border-[#74B49B];
  }
  
  .about-stat {
    @apply bg-white rounded-lg p-4 shadow-sm text-center relative overflow-hidden border border-[#EDEDED];
  }
  
  .about-stat::before {
    @apply content-[''] absolute top-0 left-0 h-1 w-full;
    background: linear-gradient(90deg, #A8E6CF, #74B49B);
  }
  
  /* Project section specific styles */
  .project-card {
    @apply bg-white rounded-xl shadow-md overflow-hidden transition-all duration-300 border border-[#EDEDED] relative z-10 flex flex-col;
  }
  
  .project-card-img-container {
    @apply relative overflow-hidden;
  }
  
  .project-card-img {
    @apply w-full h-48 object-cover transition-transform duration-500;
  }
  
  .project-card:hover .project-card-img {
    @apply transform scale-105;
  }
  
  .project-card-content {
    @apply p-5 flex-grow flex flex-col;
  }
  
  .project-card-title {
    @apply text-xl font-medium text-[#333333] mb-2;
  }
  
  .project-card-description {
    @apply text-[#666666] mb-4 flex-grow;
  }
  
  .project-card-tags {
    @apply flex flex-wrap gap-2 mb-4;
  }
  
  .project-tag {
    @apply px-3 py-1 rounded-full text-xs font-medium;
    background-color: var(--color-about-accent);
    color: var(--color-accent);
  }
  
  .featured-project {
    @apply relative;
  }
  
  .featured-project::after {
    @apply content-['Featured'] absolute top-4 right-4 bg-[#E6C07A] text-white text-xs px-3 py-1 rounded-md font-medium;
  }
  
  /* Modern UI accents */
  .accent-dot {
    @apply h-20 w-20 rounded-full absolute opacity-20 z-0;
    background: radial-gradient(circle, var(--color-primary), transparent 70%);
  }
  
  .accent-line {
    @apply h-[1px] bg-gradient-to-r from-transparent via-[#74B49B] to-transparent my-6 opacity-30;
  }

  /* Hero section components */
  .hero-content {
    @apply relative z-10 flex flex-col md:flex-row items-center justify-between w-full gap-8 py-16;
  }
  
  .hero-text {
    @apply md:w-1/2 z-10 relative;
  }
  
  .hero-title {
    @apply text-4xl md:text-5xl lg:text-6xl font-bold mb-4 leading-tight text-[#2C6E49] relative;
    text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.5);
  }
  
  .hero-subtitle {
    @apply text-xl md:text-2xl font-light mb-6 text-[#333333] max-w-xl;
  }
  
  .hero-cta-container {
    @apply flex flex-wrap gap-4 my-6;
  }
  
  .hero-image-container {
    @apply relative md:w-2/5 z-10;
  }
  
  .hero-image {
    @apply relative z-10 rounded-lg shadow-2xl border-4 border-white;
  }
  
  .hero-image::before {
    @apply content-[''] absolute inset-0 bg-[#A8E6CF] opacity-20 rounded-lg;
  }
  
  .hero-accent-shape {
    @apply absolute w-64 h-64 rounded-full opacity-20 z-0;
    background: radial-gradient(circle, var(--color-highlight) 0%, transparent 70%);
  }
  
  .hero-accent-square {
    @apply absolute w-40 h-40 bg-[#E6C07A]/10 rounded-xl z-0 border border-[#E6C07A]/20;
  }
  
  .hero-badge {
    @apply flex items-center gap-2 bg-white px-4 py-2 rounded-full shadow-md text-sm absolute font-medium;
  }
  
  /* Floating Elements Animation */
  .float-animation {
    animation: float 6s ease-in-out infinite;
  }
  
  .float-animation-delayed {
    animation: float 6s ease-in-out 2s infinite;
  }
  
  /* Testimonial Components */
  .testimonial-card {
    @apply bg-white rounded-xl shadow-md p-6 relative border border-[#EDEDED] z-10;
  }
  
  .testimonial-quote {
    @apply text-5xl text-[#A8E6CF] absolute -top-4 left-6;
  }
  
  .testimonial-text {
    @apply text-[#333333] italic mb-4 pt-4 relative z-10;
  }
  
  .testimonial-author {
    @apply flex items-center gap-3 mt-4 border-t border-[#EDEDED] pt-4;
  }
  
  .testimonial-author-image {
    @apply w-12 h-12 rounded-full border-2 border-[#A8E6CF];
  }
  
  .testimonial-author-name {
    @apply font-medium text-[#333333];
  }
  
  .testimonial-author-position {
    @apply text-sm text-[#666666];
  }

  .testimonial-slider {
    @apply flex gap-6 py-8 overflow-x-auto snap-x scrollbar-hide pb-4;
  }
  
  .testimonial-slide {
    @apply min-w-[300px] md:min-w-[350px] snap-center;
  }
}

/* Animations */
.fade-in {
  animation: fadeIn 0.5s ease-in forwards;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes soft-border-animate {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
  100% { transform: translateY(0px); }
}

/* Remove scanlines - not appropriate for soft design */
.scanlines {
  display: none;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: var(--color-bg-primary);
}

::-webkit-scrollbar-thumb {
  background: var(--color-secondary);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-accent);
}

/* Hide scrollbar for Chrome, Safari and Opera */
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.scrollbar-hide {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* Add this at the end of the file */

/* Debugging helpers */
.debug-outline * {
  outline: 1px solid rgba(255, 0, 0, 0.1);
}

#testimonials {
  /* Add a distinct background to make it clearly visible */
  background-color: var(--color-testimonial-bg);
  position: relative;
  overflow: hidden;
  /* Add a minimum height to ensure visibility */
  min-height: 50vh;
  /* Add border to help with visibility */
  border-top: 3px solid var(--color-primary);
  border-bottom: 3px solid var(--color-primary);
}

/* Ensure the navbar styling is applied */
.navbar {
  background-color: var(--color-primary) !important;
}

/* Ensure the navbar styling is applied with the new accent color */
.navbar {
  background-color: var(--color-accent) !important;
}

/* Add this if you want to create a placeholder for the resume file */
.resume-placeholder {
  padding: 20px;
  text-align: center;
  max-width: 500px;
  margin: 40px auto;
  background-color: var(--color-bg-primary);
  border: 2px dashed var(--color-accent);
  border-radius: 8px;
}
