.footer {
  background-color: #152238; /* Changed the background color */
  color: #f8f9fa;
  padding: 3rem 0 1rem;
  position: relative;
  width: 100%;
}

/* Add a subtle gradient overlay */
.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(28, 58, 148, 0.1) 0%, rgba(44, 110, 73, 0.2) 100%);
  z-index: 1;
}

.footer .container {
  position: relative;
  z-index: 2;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 2rem;
}

.footer-section {
  flex: 1;
  padding: 0 1rem;
  min-width: 250px;
  margin-bottom: 1.5rem;
}

.footer-section h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  position: relative;
  padding-bottom: 0.5rem;
}

.footer-section h3::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
  height: 2px;
  background-color: #2C6E49; /* Changed accent color to match theme */
}

.about p {
  margin-bottom: 1rem;
  line-height: 1.6;
}

.contact p {
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
}

.contact p i {
  margin-right: 0.5rem;
  color: #4CAF50;
}

.links ul {
  list-style: none;
  padding-left: 0;
}

.links ul li {
  margin-bottom: 0.5rem;
}

.links ul li a {
  color: #f8f9fa;
  text-decoration: none;
  transition: color 0.3s ease;
}

.links ul li a:hover {
  color: #4CAF50;
  padding-left: 5px;
}

.social-icons {
  display: flex;
  gap: 1rem;
}

.social-icons a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 40px;
  background-color: #2b2b2b;
  border-radius: 50%;
  color: #f8f9fa;
  transition: all 0.3s ease;
}

.social-icons a:hover {
  background-color: #4CAF50;
  transform: translateY(-3px);
}

.footer-bottom {
  text-align: center;
  padding-top: 1rem;
  border-top: 1px solid #1e304f; /* Changed border color to match new background */
}

@media screen and (max-width: 768px) {
  .footer-content {
    flex-direction: column;
  }
  
  .footer-section {
    margin-bottom: 2rem;
  }
}
